.ceat-container
{
    background-color: #fafafa;
    overflow: auto;
}
.ceat-header{
    color: white;
    padding: 1rem;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.ceats-rows{
    margin: 0 auto;
    width: 60%;
    padding: 10px 0px;
}
.ceat-row
{
    display: flex;
   justify-content: space-between;
    color: #4abd5d ;
    margin-bottom: 5px;
}
.ceat{

   display: flex;
   flex-wrap: wrap;
   
}
.ceat-no2
{
    height: 25px;
    width: 25px;
    margin-left: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    font-size: 14px;
    color: #ccc;
}
.ceat-no{
    height: 25px;
    width: 25px;
    margin-left: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border:1px solid #4abd5d;
    border-radius: 2px;
    font-size: 10px;
    color: #4abd5d;
    background-color: white;
}
.ceat-no:hover{
    background-color: #4abd5d;
    color: white;

}
.last-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.box{
    height: 25px;
    width: 100px;
    background-color: #ccc;
    transform: skew(40deg);
}
.seat-legent-wrapper{
    display: flex;
    justify-content: center;
    background-color: white;
}
.seat-legent-container{
    width: 60%;
    display: flex;
    flex-direction: column;
    padding: 2rem 0rem;
}
.seat-legent-inner{
    display: flex;
    justify-content: space-around;  
    padding: 1rem; 
}       
.seat-legent{
    display: flex;
    align-items: center;
}
.seat-box{
    height: 20px;
    width: 20px;
    border-radius: 2px;
    margin-right: 0.5rem;
}
.box1{
    background-color: gray;
}
.box2{
    border: 1px solid #4abd5d;
}
.box3{
    background-color: #4abd5d;
}
.changebg1{
    background-color: #4abd5d;
    color: white;
    
}
.seat-left-gap:nth-child(11)
{
    margin-right: 40px;
}
.ceat-row:nth-child(10),.ceat-row:nth-child(14),.ceat-row:nth-child(16)
{
    margin-bottom: 40px;
}
.ceat-row:nth-child(15) .ceat-no,.ceat-row:nth-child(16) .ceat-no{
    border: 1px solid gray;
    background-color: gray;
    color: white;
}
.diabled
{
    background-color: gray;
    color: white;
}
.selected {
    background-color:#1ea83c;
    color: white;
  }
 
.available:hover {
    background-color: rgb(70, 232, 29);
    color: #fff;
  }
